@import "../common/styles/sass/variables.sass";
@import '../common/styles/sass/mixins';

.mainBlock {
  min-height: 100vh;
  //min-height: 100vh
  display: flex;
  justify-content: center;
  flex-direction: column;
 }  //border: 1px solid #BDF598FF


.particles {
  position: fixed;
  z-index: -1;
  //right: 0
  //left: 0
  //bottom: 0
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat; }


.greeting {
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media screen and (max-width: 749px) {
    margin-bottom: 50px; }

  .nikita {
    color: $accentColor; }

  span {
    color: white; }

  h1 {
    color: white;
    font-size: 50px;
    letter-spacing: -0.2px;
    margin-bottom: 43px;
    font-weight: 300; }

  p {
    color: white; } }

.photo {
  width: 300px;
  height: 300px;
  //background-image: url("https://wpriverthemes.com/drake/wp-content/themes/drake/assets/images/me.jpg")
  background-image: url("../img/imageedit_1_5051929215.jpg");


  border-radius: 30px;
  background-size: cover;
  position: relative;

  @media screen and (max-width: 400px) {
    width: 260px;
    height: 260px; }
  &:before {
    content: '';
    position: absolute;
    height: 110%;
    width: 105%;
    border: 5px solid $accentColor;
    border-radius: 30px;
    left: 15px;
    top: -15px;
    z-index: -1; } }



.container {
  @include container;
  height: 500px;
  justify-content: space-evenly;
  //border: 1px solid $accentColor
  border-radius: 30px;


  @media screen and (max-width: 749px) {
    justify-content: center; } }

.typingText {
  width: 180px;
  height: 20px;
  display: flex;
  justify-content: left; }

