@import "src/common/styles/sass/variables.sass";

.elementButton {
  background-color: #969595;
  border: none;
  border-radius: 30px;
  color: white;
  padding: 15px 32px;
  text-align: center;
  display: inline-block;
  font-size: 14px;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  &:hover {

    box-shadow: 0 4px 8px rgba(45, 35, 66, 0.4), 0 7px 13px -3px rgba(45, 35, 66, 0.3), inset 0px -3px 0px #e7e7e7;
 }    //transform: translateY(-2px)

  &:active {
    box-shadow: none;
    transform: translateY(2px); } }


.mainButton {
  background: none;
  color: $accentColor;
  text-align: center;
  padding: 11px 58px 10px 58px;
  align-items: center;
  border-radius: 30px;
  justify-content: center;
  text-transform: uppercase;
  font-size: 18px;
  transition: 0.3s;
  cursor: pointer;
  //border: 2px solid $accentColor
  position: relative;
  z-index: 0;
  display: flex;
  border: none;
  &::after {
    content: '';
    position: absolute;
    width: 30%;
    height: 5px;
    background-color: $accentColor;
    bottom: 0;
    z-index: -1;
    transition: all 0.2s ease-in-out;
    border: 2px solid $accentColor;
    border-radius: 30px; }
  &:hover {
    //background-color: transparent
    color: #ffffff;
    &:after {
      width: 100%;
      height: 100%;
      background-color: transparent;
      transition: all 0.3s ease-in-out 0s; } } }





