.nav {
  display: flex;
  flex-direction: column;
  border: 1px solid #575757;
  border-radius: 30px;
  position: fixed;
  right: 68px;
  top: calc(50% - 111px);
  width: 55px;
  text-align: center;
  gap: 20px;
  padding: 24px 0;
  background: $backgroundColor;
  @media screen and (max-width: 1100px) {
    display: none; }





  a {
    color: $textColorMain;
    cursor: pointer;
    text-decoration: none; }
  .active {
    color: $accentColor; } }

