  justify-content: space-between;
 }  //border: 1px solid #BDF598FF

.cube {
  height: 100%;
  width: 20%;
  background-color: $textColorSecondary;
 }  //border: 1px solid #BDF598FF

.socialIcons {
  display: flex;
  height: 50px;
  width: 300px;
  justify-content: space-evenly;
  //border: 1px solid #BDF598FF
  color: red; }

