@mixin container() {
  width: 80%;
  max-width: 1130px;
  height: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 auto;
  @media screen and (max-width: 474px) {
    width: 100%; } }

@mixin flex($direction, $justifyContent, $alignItems, $wrap) {
  display: flex;
  flex-direction: $direction;
  justify-content: $justifyContent;
  align-items: $alignItems;
  flex-wrap: $wrap; }


